html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

body, h2, p {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  text-decoration: none;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__item:not(:last-of-type) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 4px;
  width: 100%;
  height: 5px;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.cards-container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.card__product-name {
  color: #060b35;
  width: 166px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition-duration: .3s;
}

.card:hover {
  cursor: pointer;
  transition-duration: .3s;
  transform: scale(1.2);
}

.card:hover .card__product-name {
  color: #34568b;
  transition-duration: .3s;
}

.card__product-foto {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__name-of-price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__count {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__btn {
  box-sizing: border-box;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

